<template>
    <div class="mySeek">
        <div class="seekHeard">
            <span class="iconfont icon-arrow-fine-up-copy" @click="goBack"></span>
            <van-search v-model="value"  @input="ff_input" placeholder="歌名、歌手、歌单" ></van-search>
        </div>
        <div class="his">
            <van-skeleton  title row-width=100%   :loading="loading">
                  <span class="hisText" v-for="(item,index) in his" @touchstart ="ff_start(index)"  @touchend="ff_end"  @click.prevent ="ff_huoq(item)" :key="index">{{item}}</span>
            </van-skeleton>
        </div>
        <div class="hots">
            <div class="hotHeard"><span>热搜榜</span></div>
            <div class="hotList">
                <div v-for="(item,index) in hotList" :key="index" class="hotItems" @click="seekItem(item.searchWord)">
                    <van-skeleton  title row-width=100%   :loading="loading">
                            <span class="seartitle"  :class="index<=2 ? 'attartitle' : ''" >{{index+1}}</span>
                            <span class="searchWord" :class="index<=2 ? 'attarWord' : ''" >{{item.searchWord}}</span>
                            <span v-if="item.score>=950000" class="a1 hotIcon">HOT</span>
                            <span v-if="item.iconType>=1" class="a1 iconfont icon-shangsheng"> </span>
                    </van-skeleton>
                   
                </div>
            </div>
        </div>
         
         <!-- 搜索显示栏 -->
        <transition 
           enter-active-class="animate__animated animate__bounceInRight" 
           leave-active-class="animate__animated animate__bounceOutRight"
        >
            <ul class="search" v-if="isSearch">
                <li class="searchHeard">
                    搜索“{{value}}”
                </li>
                <li class="searchItem" v-for="(Item,index) in  newList" :key="index" @click="ff_danSong(Item.id)">
                    <van-icon name="search" color="#babbbe" />
                    <span class="itemName">{{Item.name}}</span>
                </li>
            </ul>

        </transition>
        
        
    </div>
</template>

<script>
    import '../assets/less/Seek.less'
    export default {
        name : 'Seek',
        data() {
            return {
                value     : '',
                hotList   : [],
                input     : null,
                newList   : [],
                isSearch  : false,
                his       : [],
                isDansong : false,
                isStart   : false,
                clocks    : null,
                nums      : 0,
                loading   : true,
            }
        },
        computed:{
            queue(){
                return this.$store.state.queue;
            },
            music(){
                return this.$store.state.song;
            }
        },
        created(){
            this.$toast.loading({
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
            });
            let his = JSON.parse(sessionStorage.getItem('his'))
            if(his){
               this.his = his;
            }
            

            this.axios({
                url : '/search/hot/detail'
            })
            .then(res=>{
                this.hotList =  res.data.data;
                this.$toast.clear();
                console.log(this.hotList);
                setTimeout(()=>{
                    this.loading = false;
                },500)
            })
            .catch(err=>{
                console.log(err)
            })
        },
        methods:{
            // 搜索逻辑
            ff_input(){
                clearTimeout(this.input);
                if(this.value){
                    this.input = setTimeout(()=>{
                        // 获取搜索
                        this.ff_huoq(this.value)
                    },200)
                }else{
                    this.isSearch = false;
                }
               
            },
            // 歌曲详情
            ff_danSong(ids){
                this.isDansong = true;
                this.axios({
                    url : '/song/detail',
                    method : 'GET',
                    params : {
                        ids
                    }
                })
                .then(res=>{
                    console.log(res)
                    if(res.data.code == 200){
        
                        let listObj = {
                            //  歌名 
                            songName  : res.data.songs[0].name,
                            //  歌id
                            songId    : res.data.songs[0].id,
                            //  歌曲封面图片
                            songUrl   : res.data.songs[0].al.picUrl,
                            //  歌手 
                            autName   : res.data.songs[0].ar[0].name,
                            //  歌曲简介
                            songBrief : res.data.songs[0].al.name
                        }
                      
                       this.ff_songs(listObj.songId,listObj.songName,listObj.songUrl)
                       this.isSearch = false;
                    }     
                })             
                .catch(err=>{
                    console.log(err)
                })
            },
            // 获取歌曲url
            ff_songs(id,name,imgUrl,auto,Brief,isSingle=false){
                 console.log(id,name,imgUrl)
                 this.axios({
                     url : '/song/url',
                     method : 'GET',
                     params : {
                         id
                     }
                 })
                 .then(res=>{
                     console.log(res)
                     if(res.data.code == 200){
                           this.isDansong = false;
                           let a = {};
                          
                           a.name     = name;
                           a.url      = res.data.data[0].url;
                           a.imgUrl   = imgUrl;
                           a.id       = id;
                           a.isFlash  = true;
                           a.isPlay   = true;
                           this.musicObj = a;
                           this.$store.dispatch('gaiMusic',this.musicObj);
                            
                          console.log(this.music)
                        // 判断是否单曲加入队列
                         if(isSingle){
                             let ispduan =  true;
                             this.queue.forEach((Item,index)=>{
                                 if(Item.songId == id){
                                       ispduan = false;
                                       this.queue.splice(index,1);
                                       this.queue.unshift({
                                            songId    : id,
                                            songName  : name,
                                            songUrl   : imgUrl,
                                            autName   : auto,
                                            songBrief : Brief,
                                        })

                                 }
                             });
                            if(ispduan){
                                this.queue.unshift({
                                    songId    : id,
                                    songName  : name,
                                    songUrl   : imgUrl,
                                    autName   : auto,
                                    songBrief : Brief,
                                })
                               
                                let a =  this.queue.slice(0,30);
                                this.$store.dispatch('gaiQueue', a)

                            }
                           
                             sessionStorage.setItem('queue',JSON.stringify(this.queue))
                             this.isSingle = false;

                         }
                           
                          
                           
                     }
                 })
                 .catch(err=>{
                     console.log(err)
                 })
            },
            // 回退
            goBack(){
                this.$router.go(-1);
            },
            // 获取关键词搜索
            ff_huoq(keywords){
                this.value = keywords;
                this.axios({
                            url : '/search',
                            params : {
                                keywords 
                            }
                        })
                        .then(res=>{
                            if(res.data.code == 200){
                                this.newList = res.data.result.songs.splice(0,10);
                                this.isSearch = true;
                                console.log(this.newList)
                            }
                        })
                        .catch(err=>{
                            console.log(err)
                        })
            },
            ff_start(index){
                this.clocks = setInterval(()=>{
                          this.nums++;
                          if(this.nums>=10 && this.nums<=15){
                              this.ff_rovemItem(index) 
                          }
                },100)
                
                
            },
            ff_end(){
                clearInterval(this.clocks);
                this.nums = 0
    
            },
            
            // 删除历史关键字
            ff_rovemItem(index){
                this.his.splice(index,1);
                sessionStorage.setItem('his',JSON.stringify(this.his))
                clearInterval(this.clocks);
                this.nums = 0
            },
            // 热榜关键词搜索
            seekItem(item){
                console.log(item)
                  // 获取关键词搜索
                  this.ff_huoq(item)
            }
        },
        watch:{
           isDansong(news,olds){
               if(news){
             
                    for(let i=0;i<this.his.length;i++){
                        if(this.his[i]==this.value){
                            return
                        }
                    }
                    this.his.unshift(this.value)
                    this.his = this.his.slice(0,10);
                    sessionStorage.setItem('his',JSON.stringify(this.his));
                    this.value = '';
               }
           }
        }
    }
</script>

<style lang="less" scoped>
</style>